import React, { useEffect } from 'react'
import axios from 'axios'
import { Modal } from 'react-bootstrap';
import Modaljs from '../Admin_LuuTruXe/components/Modal'
import { makeStyles } from '@material-ui/core/styles';
import LinearProgress from '@material-ui/core/LinearProgress';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Icon from '@material-ui/core/Icon';
import { Button } from "@material-ui/core";
import UpdateProfile from './Admin_UpdateProfile';
import AddProfile from './Admin_addProfile';
import { API_HOST } from 'config/config'
import useAuth from 'stores/Author/AuthStore'
import moment from 'moment';



const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
  createButton: {
    color: '#5bc0de',
    marginLeft: '20px'

  }
});


const Admin_quanlyuser = () => {
  //MODAL Update Profile

  const [isLoading, setIsLoading] = React.useState(false);
  const [userId, setUserId] = React.useState('');
  const [isOpen, setIsOpen] = React.useState(false);
  const [isOpen_2, setIsOpen_2] = React.useState(false);
  const [isOpen_3, setIsOpen_3] = React.useState(false);
  const [isRefresh, setIsRefresh] = React.useState(false);
  const [listUser, setListUser] = React.useState([]);
  const [stateAuth,] = useAuth();

  const XoaUserModal = (props) => {
    return (
      <Modal
        show={props.isOpen}
        onHide={props.toggle}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title>Xóa hồ sơ công dân</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          Bạn muốn xóa hồ sơ công dân này
          </Modal.Body>
        <Modal.Footer>
          <Button onClick={props.toggle}>
            Hủy bỏ
            </Button>
          <Button variant="contained" color="secondary" onClick={() => deleteUser(props.id)}>
            Xóa
            </Button>
        </Modal.Footer>

      </Modal>
    );
  }

  const classes = useStyles();


  const deleteUser = async (idUser) => {
    await axios.delete(`${API_HOST}/citizenship-profiles/${idUser}`, {
      headers: {
        Authorization: 'Bearer ' + stateAuth.accessToken
      }
    })
    setIsOpen_2(false);
    setIsRefresh(!isRefresh);
  }



  useEffect(() => {
    (async () => {
      setIsLoading(true);
      await axios.get(`${API_HOST}/citizenship-profiles`, {
        headers: {
          Authorization: 'Bearer ' + stateAuth.accessToken
        }
      })
        .then(res => {
          setListUser(res.data)
          
        })
        .catch(err => console.log(err))
        .finally(() => {
          setIsLoading(false);
        })
    })()
    // eslint-disable-next-line
  }, [isRefresh])


  const editUser = (idUser) => {
    setIsOpen(true);
    setUserId(idUser)
  }

  const addUser = () => {
    setIsOpen_3(true);
  }

  const cfg_deleteUser = (idUser) => {
    setIsOpen_2(true);
    setUserId(idUser);
  }

  const handleCloseModal = () => {
    setIsOpen_2(false);
    setUserId('');
  }


  const handleAfterUpdate = () => {
    setIsOpen(false);
    setIsOpen_3(false);
    setIsRefresh(!isRefresh)
  }

  return (
    <>
      {isLoading && <LinearProgress color="secondary" />}

      <Modaljs
        header={"Cập nhật hồ sơ"}
        show={isOpen}
        onHide={() => {
          setIsOpen(false);
        }}
      >
        <UpdateProfile idUser={userId} btnCapNhat={handleAfterUpdate}/>
      </Modaljs>

      <Modaljs
        header={"Thêm hồ sơ"}
        show={isOpen_3}
        onHide={() => {
          setIsOpen_3(false);
        }}
      >
        <AddProfile btnCapNhat={handleAfterUpdate} />
      </Modaljs>
        
      <XoaUserModal isOpen={isOpen_2} id={userId} toggle={handleCloseModal} />
      <TableContainer component={Paper}>
      
        <Table className={classes.table} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>ID</TableCell>
              <TableCell align="left">Họ tên</TableCell>
              <TableCell align="left">Ngày sinh</TableCell>
              <TableCell align="left">Địa chỉ</TableCell>
              <TableCell align="left">Số điện thoại</TableCell>
              <TableCell align="left">CMND</TableCell>
              <TableCell align="left"> Quản lý
              <Button                
                className={`${classes.createButton} createButton`}
                onClick={() => addUser()}
              >
              <Icon className="fa fa-plus-circle"/>
              </Button>
              </TableCell>
              
              
            </TableRow>
          </TableHead>
          <TableBody>
            {listUser.map((user) => (
              <TableRow key={user.id}>
                <TableCell component="th" scope="row">
                  {user.id}
                </TableCell>
                <TableCell align="left">{user.firstName} {user.lastName}</TableCell>
                <TableCell align="left">{moment(user.dateOfBirth).format("YYYY-MM-DD")}</TableCell>
                <TableCell align="left">{user.addresses}</TableCell>
                <TableCell align="left">{user.phoneNumber}</TableCell>
                <TableCell align="left">{user.idNo}</TableCell>
                <TableCell align="left">

                  {/* Cap nhat ho so cong dan */}
                  <Button
                    variant="contained"
                    color="primary"
                    className={classes.button}
                    onClick={() => editUser(user.id)}
                  >
                    <span className="material-icons">edit</span>
                  </Button>

                  {/* Xoa ho so cong dan */}
                  <Button
                    variant="contained"
                    color="secondary"
                    className={classes.button}
                    onClick={() => cfg_deleteUser(user.id)}
                  >
                    <span className="material-icons">
                      delete
                    </span>
                  </Button>

                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>

      </TableContainer>
    </>
  );

}

export default Admin_quanlyuser
