import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import axios from 'axios'
import useAuth from 'stores/Author/AuthStore'
import {API_HOST} from 'config/config'
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import { Button } from "@material-ui/core";
import { Modal } from 'react-bootstrap';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';

const useStyles = makeStyles((theme) => ({
  root: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: '45%',
    },
  },
}));


export default function AddProfile ({btnCapNhat}){
   const [stateAuth, ] = useAuth();
   const classes = useStyles();
   
  const [dataUser, setdataUser] = React.useState({
      firstName: "",
      lastName: "",
      idNo: "",
      dateOfBirth: new Date('2014-08-18'),
      phoneNumber: "",
      addresses: "",
      gender: "",
      avatarUrl: "string"
  })

  const {firstName, lastName, idNo, dateOfBirth, phoneNumber, addresses, gender} = dataUser;
  const onInputChange = (e) => {
    setdataUser({ ...dataUser, [e.target.name]: e.target.value})
  }

  const handledateOfBirthChange = (date) => {
    setdataUser({...dataUser,dateOfBirth: date})
  };

 

  const addUser = async () => {
    const dataStr = JSON.stringify(dataUser)
    if(dataStr.includes(`""`)){
      alert("Vui Lòng Điền Đầy Đủ Thông Tin.")
      return;
    }
    await axios.post(`${API_HOST}/citizenship-profiles`,dataUser, {
      headers: {
        Authorization: 'Bearer ' + stateAuth.accessToken
      },
      })
      .catch(err => {})
      .finally(() => {
        btnCapNhat()
      })
  }


    return (
      <>
      <Modal.Body>
      <form className={classes.root} noValidate autoComplete="off">
      <TextField id="standard-basic" label="Tên" name="firstName"
       value={firstName} 
       onChange={e => onInputChange(e)}
       />
      <TextField id="standard-basic" label="Họ" name="lastName"
       value={lastName}
       onChange={e => onInputChange(e)}
       />
       <TextField id="standard-basic" label="CMND" name="idNo"
       value={idNo}
       onChange={e => onInputChange(e)}
       />
      <MuiPickersUtilsProvider utils={DateFnsUtils}>
        <KeyboardDatePicker
          disableToolbar
          variant="inline"
          format="MM/dd/yyyy"
          margin="normal"
          id="date-picker-inline"
          value={dateOfBirth}
          onChange={handledateOfBirthChange}
          KeyboardButtonProps={{
            'aria-label': 'change date',
          }}
        />
      </MuiPickersUtilsProvider>
      <TextField id="standard-basic" label="Số điện thoại" name="phoneNumber"
       value={phoneNumber}
       onChange={e => onInputChange(e)}
       />
      <TextField id="standard-basic" label="Địa chỉ" name="addresses"
       value={addresses}
       onChange={e => onInputChange(e)}
       />
      <TextField id="standard-basic" label="Giới tính" name="gender" 
      value={gender}
      onChange={e => onInputChange(e)}
      />
    </form>
    </Modal.Body>
    <Modal.Footer>   
        <Button
            variant="contained"
            color="secondary"
            className={classes.button}
            onClick={() => addUser()}
          >
            Thêm
          </Button>
      </Modal.Footer>
    </>
    )
}
