import React, {useEffect}  from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import axios from 'axios'
import useAuth from 'stores/Author/AuthStore'
import {API_HOST} from 'config/config'
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import { Button } from "@material-ui/core";
import { Modal } from 'react-bootstrap';

const useStyles = makeStyles((theme) => ({
  root: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: '45%',
    },
  },
}));


const UpdateProfile = ({idUser, btnCapNhat}) => {
  const [infoUser, setinfoUser] = React.useState(null);
  const [stateAuth, ] = useAuth();

  const classes = useStyles();
  useEffect(() => {
    (() => {
       axios.get(`${API_HOST}/citizenship-profiles/${idUser}`, {
        headers: {
          Authorization: 'Bearer ' + stateAuth.accessToken
        }
      })
        .then(res => {
         setinfoUser(res.data)
        })
        .catch(err => console.log(err))
    })()
    // eslint-disable-next-line
  }, [])

  const onInputChange = (e) => {
    setinfoUser({ ...infoUser, [e.target.name]: e.target.value})
  }

  const updateProfile = async (idUser) => {
    const {firstName, lastName, dateOfBirth, phoneNumber, addresses, gender} = infoUser
    const dataPut = {firstName, lastName, dateOfBirth, phoneNumber, addresses, gender}
    await axios.put(`${API_HOST}/citizenship-profiles/${idUser}`,dataPut, {
      headers: {
        Authorization: 'Bearer ' + stateAuth.accessToken
      }
    })
      .catch(err => console.log(err))
      .finally(() => {
        btnCapNhat()
      })
  }

    return (
      <>
      <Modal.Body>
      {infoUser ? <form className={classes.root} noValidate autoComplete="off">
      <TextField id="standard-basic" label="Tên" name="firstName"
       value={infoUser.firstName} 
       onChange={e => onInputChange(e)}
       />
      <TextField id="standard-basic" label="Họ" name="lastName"
       value={infoUser.lastName}
       onChange={e => onInputChange(e)}
       />
      <TextField id="standard-basic" label="Ngày sinh" name="dateOfBirth"
       value={infoUser.dateOfBirth}
       onChange={e => onInputChange(e)}
       />
      <TextField id="standard-basic" label="Số điện thoại" name="phoneNumber"
       value={infoUser.phoneNumber}
       onChange={e => onInputChange(e)}
       />
      <TextField id="standard-basic" label="Địa chỉ" name="addresses"
       value={infoUser.addresses}
       onChange={e => onInputChange(e)}
       />
      <TextField id="standard-basic" label="Giới tính" name="gender" 
      value={infoUser.gender}
      onChange={e => onInputChange(e)}
      />

    </form> : <p>Loading</p>}
    </Modal.Body>
    <Modal.Footer>   
        <Button
            variant="contained"
            color="secondary"
            className={classes.button}
            onClick={() => updateProfile(idUser)}
          >
            Cập nhật
          </Button>
      </Modal.Footer>
    </>
    )
}
export default UpdateProfile;