import React, { useState, useEffect } from "react";
import 'bootstrap/dist/css/bootstrap.min.css';

import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';

import axios from 'axios'
import APIROUTE from '../Constants/ApiRoute'
import Loading from './Loading'
import useAuth from 'stores/Author/AuthStore'
import { API_HOST } from 'config/config'
import { Modal } from 'react-bootstrap';
import { Button } from "@material-ui/core";


const CapNhatBanGhi = ({ IDBanGhi , isOpen, handleUpdateXe} ) => {
  const [openloading, setOpenloading] = useState(false);
  const [allVehicles, setAllVehicles] = useState([]);
  const [stateAuth,] = useAuth();
  const [Vehicle, setVehicle] = useState(0);

  useEffect(() => {
    (async () => {
      setOpenloading(true);
      await axios.get(`${API_HOST}${APIROUTE.VEHICLES}`, {
        headers: {
          Authorization: 'Bearer ' + stateAuth.accessToken
        }
      })
        .then(res => {
          setAllVehicles(res.data);
        })
        .catch(err => {})
      setOpenloading(false);
    })()
  }
    // eslint-disable-next-line
    , [null])

  const onHandleUpdate = async(IDBanGhi) => {
    var data = JSON.stringify({"vehicleId":Vehicle.id});
    var config = {
      method: 'put',
      url: `${API_HOST}${APIROUTE.RECORD_PROFILE}/${IDBanGhi}`,
      headers: {
        'Authorization': 'Bearer ' + stateAuth.accessToken,
        'Content-Type': 'application/json',
        'Cookie': 'ARRAffinity=b6c882a5e013db277ef434fb044e8255f520bbdc70059876373bab098d534452; ARRAffinitySameSite=b6c882a5e013db277ef434fb044e8255f520bbdc70059876373bab098d534452'
      },
      data: data
    };
    axios(config).then()
      .catch(err => {})
      .finally(() => {
        handleUpdateXe()
      });
 }

  return (
    <div>
      <Modal.Body>
        <div className="container">
          <Loading open={openloading} />
          <div className="row">Vui lòng chọn biển số xe {Vehicle?.id || ''}</div>
          <div className="row">
            <Autocomplete
              id="combo-box-demo"
              options={allVehicles}
              getOptionLabel={(option) => String(option.numberPlate)}   
              renderOption={(option) => <span>{option.brand} -- {option.numberPlate}</span>}
              onChange={(evt, value) => setVehicle(value)}
              style={{ width: 900 }}
              renderInput={(params) => <TextField {...params} label="Biển số" variant="outlined"  
              />}
            />
          </div>
        </div>
      </Modal.Body>
      <Modal.Footer>   
        <Button
          onClick={()=>onHandleUpdate(IDBanGhi)}
        >Xác nhận cập nhật</Button>
      </Modal.Footer>
    </div>
  );
};

export default CapNhatBanGhi;
